.languages_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
}

.catalan {
    grid-area: 1 / 1 / 2 / 2; 
    border: 1px solid #94692d;
    padding: 20px;
    border-radius: 10px;
}

.spanish {
    grid-area: 1 / 2 / 2 / 3; 
    border: 1px solid #94692d;
    padding: 20px;
    border-radius: 10px;
}

.english {
    grid-area: 2 / 1 / 3 / 2;
    border: 1px solid #94692d;
    padding: 20px;
    border-radius: 10px;
}

.french {
    grid-area: 2 / 2 / 3 / 3;
    border: 1px solid #94692d;
    padding: 20px;
    border-radius: 10px;
}